.navbar {
    width: 100%;
    height: 100px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    background-color: white;
    
    display: flex;
    justify-content: space-between;
    align-items: center;

    position: fixed;
    z-index: 1000;
}


.logo-design {
    margin-left: 5%;
    width: 250px;
}

.navbar-items {
    display: flex;
    list-style: none;
    margin: 0;
    padding: 0;

    font-size: 25px;
}

.navbar-items li {
    margin: 0 15px;
    cursor: pointer;
}

.intouch-button {
    height: 50%;
    background-color: #00ACC1;
    cursor: pointer;
    border: none;
    border-radius: 5px;
    font-size: large;
    color: white;
    margin-right: 100px;
}

.intouch-button:hover {
    background-color: #018698;
}

.hamburger{
    display: none;
}

.hamburger-link{
    width: 100%;
    height: 0;
    overflow: hidden;
    transition: all 0.5s ease;
}

@media (max-width:1100px) {        
    .navbar-items, .intouch-button{
        display: none;
    }

    .logo-design{
        margin-left: 35px;
    }
    
    .hamburger{
        display: flex;
    }

    .hamburger-link {
        background-color: white;
        
        position: absolute;
        top: 85px;
        left: 0;
        padding: 0;
    }
    
    .open {
        height: 270px;
    }
    
    .hamburger-link li{
        list-style: none;
        font-size: 1.5rem;
        margin: 13px;
        padding-left: 35px;
        cursor: pointer;
    }    

    .hamburger-icon{
        cursor: pointer;
        height: 50px;
        margin-right: 45%;
    }
}